.toDo {
  display: flex;
  margin: 0 -3rem 4px;
  padding: 1.1rem 3rem;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.1);
}

.toDo-task {
  opacity: 0.9;
  position: relative;
}

.toDo-task.completed .text {
  text-decoration: line-through;
  transition: 100ms;
  opacity: 0.6;
  color: black;
}

.container {
  display: block;
  position: relative;
  padding-left: 35px;
  cursor: pointer;
}

.checkbox {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
}

.checkmark:after {
  content: '';
  position: absolute;
  display: none;
}

.container input:checked ~ .checkmark:after {
  display: block;
}

.container .checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  transform: rotate(45deg);
}

.text {
  word-break: break-word;
}

.buttons {
  display: flex;
}

.button {
  border: none;
  font-size: 1em;
  margin: 0.3em;
  background: none;
  cursor: pointer;
  color: #fff;
}

.container:hover input ~ .checkmark {
  background-color: #ccc;
}

.container input:checked ~ .checkmark {
  background-color: #2196f3;
}

.toDo form {
  display: flex;
  flex-wrap: wrap;
}
.toDo form label {
  min-width: 100%;
  margin-bottom: 0.5rem;
  font-size: 1.3rem;
}
.toDo form input {
  flex-grow: 1;
  border: none;
  background: #f7f1f1;
  padding: 0 1.5em;
  font-size: initial;
  outline: none;
}
